<template>
        <div class="row text-center">
            <div class="col-md-3 col-sm-6 col-xs-12" :key="i.id" v-for="(item, i) in products">
                <v-hover v-slot:default="{ hover }">
                <v-card
                    class="mx-auto"
                    color="grey lighten-4"
                    max-width="600"
                    >


                            <v-img
                                class="white--text align-end"
                                :aspect-ratio="16/9"
                                height="200px"
                                :src='item.image'
                            >
                            <v-expand-transition>
                                <div
                                    v-if="hover"
                                    class="d-flex transition-fast-in-fast-out white darken-2 v-card--reveal display-3 white--text"
                                    style="height: 100%;"
                                >
                                    <v-btn v-if="hover" href="/product" class="" outlined>VIEW</v-btn>
                                </div>

                                </v-expand-transition>
                            </v-img>

                        <v-card-text style="position: relative;" class="pb-0">
                            <!-- <v-btn absolute color="red" class="white--text mt-n8" icon medium right top>
                                <v-icon>mdi-heart</v-icon>
                            </v-btn> -->
                        </v-card-text>
                        <v-card-subtitle class="pt-0 pb-0"><b>{{item.name}}</b></v-card-subtitle>

                        <v-card-text class="pb-0">
                            <v-row
                                align="center"
                                class="mx-0"
                            >
                                <v-rating
                                :value="4.5"
                                color="amber"
                                dense
                                half-increments
                                size="14"
                                readonly
                                class="mt-0"
                                ></v-rating>

                                <div class="grey--text ml-4">
                                4.5 (413)
                                </div>
                            </v-row>
                            <v-row>
                            <v-col class="my-4 subtitle-1 mb-0" style="text-align: left">
                                in stock: {{item.in_stock}}
                            </v-col>

                            <v-col class="my-4 subtitle-1 mb-0" style="text-align: right">
                                ${{item.customer_price}}
                            </v-col>
                            </v-row>

                        </v-card-text>

                        <v-divider class="mx-4 ml-0 mr-0 mt-0 mb-0"></v-divider>

                        <v-card-actions style="background-color:rgb(252, 249, 237)">
                            <v-dialog
                            transition="dialog-bottom-transition"
                            fullscreen
                            hide-overlay
                            >
                            <template v-slot:activator="{ on, attrs }">
                                <v-btn
                                color="black"
                                style="color: rgb(187, 162, 87)"
                                v-bind="attrs"
                                v-on="on"
                                >
                                ver
                                </v-btn>
                            </template>
                            <template v-slot:default="dialog">
                            <v-card>
                                <v-toolbar
                                color="black"
                                dark
                                elevation="2"
                                >
                                <v-btn
                                    icon
                                    dark
                                    @click="dialog.value = false"
                                >
                                    <v-icon small>mdi-close</v-icon>
                                </v-btn>
                                </v-toolbar>
                                <v-card-text>
                                <div class="text-h2 pa-12">Hello world!</div>
                                </v-card-text>
                                <v-card-actions class="justify-end">
                                <v-btn
                                    text
                                    @click="dialog.value = false"
                                >Close</v-btn>
                                </v-card-actions>
                            </v-card>
                            </template>
                            </v-dialog>
                            <v-spacer></v-spacer>
                            <v-btn
                            color="black"
                            to='/shop/eyelashes-products'
                            style="color: rgb(187, 162, 87)"
                            >
                            Add to Cart
                            </v-btn>
                        </v-card-actions>
                </v-card>
                </v-hover>
            </div>


        </div>
</template>

<script>
import axios from 'axios'
export default {
    props: {
        collectionsId: {
            type: Number
        },
    },
    name: 'Products',
    data() {
        return {
            products: [],
            dialog: false,
        }
    },
    methods: {
        getProducts: async function (collectionId) {
            console.log("hola hola", collectionId);
            if (collectionId == 1) {
                axios.get('api/v1.0/products/inventory')
                .then(res => {
                    console.log(res);
                    this.products = res.data.inventory.rows;
                    console.log(this.products)
                }).catch(err => console.log(err))
            } else {
                axios.get('api/v1.0/products', { params: { collectionId: collectionId } })
                .then(res => {
                    console.log(res);
                    this.products = res.data.products.rows;
                    console.log(this.products)
                }).catch(err => console.log(err))
            }

        }
    },
    created() {
        console.log(this.collectionsId);
        this.getProducts(this.collectionsId);
    },

}
</script>
