                            if (categorieValue.id === collectionValue.categoriesId) {
                                categorieValue.children.push(collectionValue);
                            }
                        })
                    })
                }).catch(err => console.log(err))
                this.collectionTest = categoriesResponse.data.categories.rows;
            }).catch(err => console.log(err))



